import fetch from './api.config.js'

export default {
  http(params) {
    return fetch.fetchPost(params.url, params.params)
  },
  nab_schedule(params) {
    return fetch.fetchGet('Nba.schedule', params)
  },
  new_list(params) {
    return fetch.fetchGet('Nba.New_list', params)
  },
  news_info(params) {
    return fetch.fetchGet('Nba.News_info', params)
  },
  news_comments(params) {
    return fetch.fetchGet('Nba.news_comments', params)
  },
  team_rank(params) {
    return fetch.fetchGet('Nba.team_rank', params)
  },
  player_top(params) {
    return fetch.fetchGet('Nba.player_top', params)
  },
  player_detail(params) {
    return fetch.fetchGet('Nba.player_detail', params)
  },
  live_detail(params) {
    return fetch.fetchGet('Nba.live_detail', params)
  },
  live_content(params) {
    return fetch.fetchGet('Nba.live_content', params)
  },
  technical_statistics(params) {
    return fetch.fetchGet('Nba.technical_statistics', params)
  },
  team_info(params) {
    return fetch.fetchGet('Nba.team_info', params)
  }
}



