<template>
  <div class="home">
    <ul class="game-list" v-if="list.length != 0">
      <li class="game-item" v-for="  list in list.data.list">
        <router-link :to="{ path: 'livedetail', query: { schid: list.schid,liveid:list.liveid }}">
          <div class="team left">
            <img :src="list.visitteamlogo" class="left">
            <div class="team-info right">
              <p class="team-score">{{list.visitscore}}</p>
              <p class="team-name">{{list.visitteamname}}</p>
            </div>

          </div>
          <div class="time left">
            {{list.match_status || list.time}}
            <br>{{list.match_time}}
          </div>
          <div class="team right">

            <div class="team-info left">
              <p class="team-score">{{list.homescore}}</p>
              <p class="team-name ">{{list.hometeamname}}</p>
            </div>
            <img :src="list.hometeamlogo" class="right">
          </div>
          <div class="team-news" v-if="list.title">
            <i class="iangle">战报</i> {{list.title}}
          </div>
        </router-link>
      </li>
    </ul>
    <div class="refesh-btn" @click="nab_schedule">
    </div>

  </div>
</template>

<script>
  import Vue from 'vue'
  import {
    Indicator
  } from 'mint-ui';
  import api from '../utils/api'


  export default {
    name: 'Home',

    data() {
      return {
        list: []
      }
    },
    created() {
      this.nab_schedule();
    },
    methods: {
      nab_schedule() {
        Indicator.open('加载中...');
        api.nab_schedule()
          .then(function (res) {
            console.log(res)
            this.list = res.data;
            Indicator.close();
          }.bind(this)).catch(function (error) {
            console.log(error)
          })

      }
    }
  }

</script>


<style scoped="scoped">
  .game-item {
    width: 100%;
    min-height: 60px;
    height: auto;
    border-bottom: 1px solid #e3e3e3;
    overflow: hidden;
    box-sizing: border-box;
    padding: 15px 10px
  }


  .time {
    width: 28%;
  }

  .team {
    width: 36%;
  }

  .time {
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .team img {
    width: 65px;
    height: 65px;
    display: block;
  }

  .team-score,
  .team-name {
    line-height: 30px;
    font-size: 13px;
  }

  .team-score {
    font-size: 20px;
  }

  .time {
    text-align: center;
    height: 65px;
  }

  .team-news {
    width: 100%;
    float: left;
    font-size: 14px;
    box-sizing: border-box;
    padding: 0 15px;
    height: 20px;
    overflow: hidden;
    text-align: left;
  }

  .iangle {
    padding: 1px 5px;
    background: #167dc6;
    color: #FFF;
    font-size: 12px;
    font-style: normal;
  }

  .refesh-btn {
    width: 44px;
    height: 44px;
    background: rgba(0, 170, 152, 0.6) url("data:image/png;base64,") center no-repeat;
    background-size: 25px;
    border-radius: 50%;
    position: fixed;
    right: 10px;
    bottom: 70px;
    font-size: 14px;
    color: #fff;
    -webkit-transition: 0.4s;
    -webkit-transition: -webkit-transform 0.4s ease-out;
    transition: transform 0.4s ease-out;
    -moz-transition: -moz-transform 0.4s ease-out;
  }

  .refesh-btn:active {
    transform: rotateZ(720deg);
    -webkit-transform: rotateZ(720deg);
    -moz-transform: rotateZ(720deg);
  }

</style>



